ul {
  margin: 0;
  padding: 0;
}
ul li {
  list-style-type: none;
}

img {
  vertical-align: top;
}

p {
  margin: 0;
  padding-bottom: 15px;
  line-height: 1.6;
}

a {
  text-decoration: none;
}

html,
body {
  min-height: 100%;
}

.italic {
  font-style: italic;
}

.underline {
  text-decoration: underline;
