.cfix:after {
  clear: both;
  content: " ";
  display: block;
  height: 0;
  line-height: 0;
  overflow: hidden;
  visibility: hidden;
}

html,
.site-wrap {
  background-position: top left;
  background-repeat: no-repeat;
  background-size: contain;
}
html:before,
.site-wrap:before {
  background-color: #000;
  display: none;
  opacity: calc(30 * 0.01);
}

.site-container {
  float: none;
  margin-left: auto;
  margin-right: auto;
  /* padding: 40px 5%; */
  max-width: 1600px;
}

html,
.site-wrap .page-background-video .video-wrap {
  left: 0;
  width: 100%;
}

.image-left .page-background-video .video-wrap {
  left: 50vw;
  width: 50vw;
}

.image-right .page-background-video .video-wrap {
  width: 50vw;
}

.example-site-footer {
  background: rgba(37, 38, 42, 0.94);
  bottom: 0;
  color: #fff;
  display: inline-block;
  font-family: "Helvetica Neue", sans-serif;
  font-size: 14px;
  left: 0;
  padding: 0 20px 0 0;
  position: fixed;
  width: 100%;
  z-index: 4;
}
.example-site-footer .icon {
  fill: #fff;
  float: left;
}
.example-site-footer .content-left,
.example-site-footer .content-right {
  height: 70px;
}
.example-site-footer .content-left {
  float: left;
}
.example-site-footer .content-right {
  float: right;
}

.example-footer-content {
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  transform: translateY(-50%);
  display: inline-block;
  position: relative;
  top: 50%;
}

.example-footer-link {
  border-bottom: 1px solid #fff;
  color: #fff;
}
.example-footer-link:hover {
  border-bottom-width: 0;
}

@media (max-width: 768px) {
  .example-site-footer .desktop {
    display: none;
  }
  .example-site-footer .mobile {
    text-transform: capitalize;
  }
}

html {
  position: relative;
}
html:before {
  content: "";
  height: 100%;
  left: 0;
  position: fixed;
  top: 0;
  width: 100%;
}

.site-wrap,
.site-container {
  position: relative;
  width: 100%;
}

.site-wrap {
  background-color: #fff;
}
.site-wrap:before {
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.site-header {
  position: relative;
}

.theme-container:before {
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.responsive-nav {
  display: none;
  opacity: 0;
  visibility: hidden;
}

.project-cover .separator:last-child,
.project-header .fields .separator:last-child {
  display: none;
}

.preserve-whitespace {
  white-space: pre-wrap;
}

.preserve-whitespace--nowrap {
  white-space: pre;
}

.page-background-video .video-wrap {
  height: 100vh;
  left: 80px;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: calc(100vw - 80px);
  z-index: -1;
}
.image-right .page-background-video .video-wrap {
  width: 50vw;
}
.image-left .page-background-video .video-wrap {
  left: 50%;
  width: 50vw;
}

.page-background-video .renditions-video {
  font-family: "object-fit: cover;";
  height: 100%;
  left: 0;
  object-fit: cover;
  position: relative;
  top: 0;
  width: 100%;
}

.page-background-video-with-panel .video-wrap {
  left: 260px;
  width: calc(100vw - 260px);
}
.image-right .page-background-video-with-panel .video-wrap {
  left: 260px;
  width: calc(50vw - 130px);
}
.image-left .page-background-video-with-panel .video-wrap {
  left: calc(50% + 130px);
  width: calc(50vw - 130px);
}

.image-right .page-background-video-with-panel .video-wrap {
  left: 0;
  width: 50vw;
}

.image-left .page-background-video-with-panel .video-wrap {
  left: 50%;
  width: 50vw;
}

.mp4-image {
  display: none !important;
  font-family: "object-fit: cover;";
  height: 100%;
  left: 0;
  object-fit: cover;
  position: relative;
  top: 0;
  width: 100%;
}

@supports (-webkit-overflow-scrolling: touch) {
  .video-wrap .renditions-video {
    display: none;
  }
  .video-wrap .mp4-image {
    display: block !important;
  }
}

.logo,
.logo-secondary {
  clear: both;
  word-wrap: break-word;
}
.logo img,
.logo-secondary img {
  max-width: 100%;
}
.logo a,
.logo-secondary a {
  color: inherit;
}
.logo .image-link,
.logo-secondary .image-link {
  display: inline-block;
  max-width: 100%;
}
.logo .image-rollover,
.logo .image-scroll,
.logo-secondary .image-rollover,
.logo-secondary .image-scroll {
  display: none;
}
.logo.logo-text,
.logo-secondary.logo-text {
  width: 100%;
}
.logo.has-rollover.hoverable:hover .image-link,
.logo-secondary.has-rollover.hoverable:hover .image-link {
  display: none;
}
.logo.has-rollover.hoverable:hover .image-link.image-rollover,
.logo-secondary.has-rollover.hoverable:hover .image-link.image-rollover {
  display: block;
}
.on-scroll .logo.has-scroll .image-normal,
.on-scroll .logo.has-scroll .image-rollover,
.on-scroll .logo-secondary.has-scroll .image-normal,
.on-scroll .logo-secondary.has-scroll .image-rollover {
  display: none !important;
}
.on-scroll .logo.has-scroll .image-scroll,
.on-scroll .logo-secondary.has-scroll .image-scroll {
  display: inline-block;
}

.social {
  font-size: 0;
}
.social li {
  display: inline-block;
}
.social li:first-child {
  padding-left: 0 !important;
}
.social li:last-child {
  padding-right: 0 !important;
}
.social li svg {
  vertical-align: middle;
}
.social a {
  display: block;
  text-align: center;
}

nav .page-title,
nav .gallery-title,
nav .project-title,
nav .link-title,
nav .social,
nav .social li {
  display: inline-block;
  vertical-align: middle;
}

nav .project-title:first-child,
nav .gallery-title:first-child,
nav .page-title:first-child,
nav .link-title:first-child {
  padding-left: 0 !important;
}

nav .project-title:last-child,
nav .gallery-title:last-child,
nav .page-title:last-child,
nav .link-title:last-child {
  padding-right: 0 !important;
}

.masthead {
  overflow: hidden;
  position: relative;
}
.masthead .video-wrap {
  height: 100%;
  left: 0;
  overflow: hidden;
  position: absolute;
  top: 0;
  width: 100%;
}
.masthead video {
  font-family: "object-fit: cover;";
  height: 100%;
  left: 0;
  object-fit: cover;
  top: 0;
  width: 100%;
}
.masthead:after {
  content: "";
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}
.masthead h1,
.masthead p {
  width: 100%;
  word-wrap: break-word;
}

.masthead,
.masthead-placeholder {
  width: 100%;
}

.masthead-placeholder {
  opacity: 0;
  position: relative !important;
  z-index: -1;
}

.masthead-contents,
.masthead-text {
  width: 100%;
}

.masthead-contents {
  height: 100%;
  top: 0;
  z-index: 1;
}

.masthead,
.masthead-contents {
  display: flex;
  -webkit-box-align: center;
  -moz-box-align: center;
  box-align: center;
  -webkit-align-items: center;
  -moz-align-items: center;
  -ms-align-items: center;
  -o-align-items: center;
  align-items: center;
  -ms-flex-align: center;
}

.masthead-arrow-container {
  cursor: pointer;
  height: 40px;
  width: 60px;
}

.masthead-arrow {
  -webkit-transform: rotate(45deg) translateY(-15px) translateX(5px);
  -moz-transform: rotate(45deg) translateY(-15px) translateX(5px);
  -ms-transform: rotate(45deg) translateY(-15px) translateX(5px);
  -o-transform: rotate(45deg) translateY(-15px) translateX(5px);
  transform: rotate(45deg) translateY(-15px) translateX(5px);
  border-bottom: 1px solid transparent;
  border-right: 1px solid transparent;
  bottom: 5px;
  height: 30px;
  position: absolute;
  width: 30px;
}

.masthead-button {
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
}
.masthead-button + .masthead-button-spacer + .masthead-button {
  margin-left: 17px;
}

.masthead-button-spacer {
  display: none;
}

@media (max-width: 540px) {
  .masthead-button + .masthead-button-spacer {
    display: block;
    height: 17px;
  }
  .masthead-button + .masthead-button-spacer + .masthead-button {
    margin-left: 0;
  }
}

.site-footer {
  clear: both;
  display: block;
}

.cookie-banner {
  bottom: 0;
  display: none;
  position: fixed;
  text-align: center;
  z-index: 99999;
}
.cookie-banner.show {
  display: block;
}
.cookie-banner p {
  font-size: 18px;
  line-height: 1.222;
  margin: 24px;
  max-width: calc(100% - 84px);
}
@media (max-width: 768px) {
  .cookie-banner p {
    font-size: 16px;
  }
}
.cookie-banner a {
  color: inherit;
  margin-left: 10px;
  text-decoration: underline;
}
.cookie-banner .close-btn {
  position: absolute;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  width: 16px;
}

